:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}
.center {
  text-align: center;
}
.container{
  width: 80%;
  margin: auto;
}
.flex{
  display: flex;
}
.spaceB{
  display: flex;
  justify-content: space-between;
}
nav{
  padding: 1rem 0;
}
ul{
  list-style: none;
}
li{
  padding: 1rem 2rem;
  font-size: 2rem;
}
.btn{
  padding: 1rem 2rem;
  color: #fff;
  font-size: 1.8rem;
  outline: none;
  border: none;
  border-radius: 3rem;
  background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
}

.btn:hover{
  color: var(--orange);
  background:white;
  border: 1px solid var(--orange);
  cursor: pointer;
}